@import url(https://fonts.googleapis.com/css?family=Montserrat);
@import url("https://fonts.googleapis.com/css?family=Anton&display=swap");
.astrodivider {
  margin: -39px auto;
  width: 100%;
  max-width: 100%;
  position: relative; }
  .astrodivider span {
    width: 50px;
    height: 50px;
    position: absolute;
    bottom: 100%;
    margin-bottom: -25px;
    left: 50%;
    margin-left: -25px;
    border-radius: 100%;
    box-shadow: 0 2px 4px #4fb39c;
    background: #fff; }
  .astrodivider i {
    position: absolute;
    top: 4px;
    bottom: 4px;
    left: 4px;
    right: 4px;
    border-radius: 100%;
    border: 1px dashed #68beaa;
    text-align: center;
    line-height: 40px;
    font-style: normal;
    color: #049372; }

.astrodividermask {
  overflow: hidden;
  height: 20px; }
  .astrodividermask::after {
    content: "";
    display: block;
    margin: -25px auto 0;
    width: 100%;
    height: 25px;
    border-radius: 125px / 12px;
    box-shadow: 0 0 8px #049372; }

@keyframes spin {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

@keyframes color-1 {
  0% {
    background-color: #eb73b9; }
  100% {
    background-color: #17ead9; } }

::selection {
  background: #6078ea;
  color: white;
  text-shadow: none; }

::-webkit-selection {
  background: #6078ea;
  color: white;
  text-shadow: none; }

.field-container {
  position: relative;
  padding: 0;
  margin: 0;
  border: 0;
  width: 275px;
  top: -111px;
  height: 40px; }

.icons-container {
  position: absolute;
  top: 5px;
  right: 4px;
  width: 35px;
  height: 35px;
  overflow: hidden; }

.icon-close {
  position: absolute;
  top: 2px;
  left: 2px;
  width: 75%;
  height: 75%;
  opacity: 0;
  cursor: pointer;
  transform: translateX(-200%);
  border-radius: 50%;
  transition: opacity 0.25s ease, transform 0.43s cubic-bezier(0.694, 0.048, 0.335, 1); }
  .icon-close:before {
    content: "";
    border-radius: 50%;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    border: 2px solid transparent;
    border-top-color: #6078ea;
    border-left-color: #6078ea;
    border-bottom-color: #6078ea;
    transition: opacity 0.2s ease; }
  .icon-close .x-up {
    position: relative;
    width: 100%;
    height: 50%; }
    .icon-close .x-up:before {
      content: "";
      position: absolute;
      bottom: 2px;
      left: 3px;
      width: 50%;
      height: 2px;
      background-color: #6078ea;
      transform: rotate(45deg); }
    .icon-close .x-up:after {
      content: "";
      position: absolute;
      bottom: 2px;
      right: 0px;
      width: 50%;
      height: 2px;
      background-color: #6078ea;
      transform: rotate(-45deg); }
  .icon-close .x-down {
    position: relative;
    width: 100%;
    height: 50%; }
    .icon-close .x-down:before {
      content: "";
      position: absolute;
      top: 5px;
      left: 4px;
      width: 50%;
      height: 2px;
      background-color: #6078ea;
      transform: rotate(-45deg); }
    .icon-close .x-down:after {
      content: "";
      position: absolute;
      top: 5px;
      right: 1px;
      width: 50%;
      height: 2px;
      background-color: #6078ea;
      transform: rotate(45deg); }
  .is-type .icon-close:before {
    opacity: 1;
    animation: spin 0.85s infinite; }
  .is-type .icon-close .x-up:before, .is-type .icon-close .x-up:after {
    animation: color-1 0.85s infinite; }
  .is-type .icon-close .x-up:after {
    animation-delay: 0.3s; }
  .is-type .icon-close .x-down:before, .is-type .icon-close .x-down:after {
    animation: color-1 0.85s infinite; }
  .is-type .icon-close .x-down:before {
    animation-delay: 0.2s; }
  .is-type .icon-close .x-down:after {
    animation-delay: 0.1s; }

.icon-search {
  position: relative;
  top: 5px;
  left: 8px;
  width: 50%;
  height: 50%;
  opacity: 1;
  border-radius: 50%;
  border: 3px solid #c7d0f8;
  transition: opacity 0.25s ease, transform 0.43s cubic-bezier(0.694, 0.048, 0.335, 1); }
  .icon-search:after {
    content: "";
    position: absolute;
    bottom: -9px;
    right: -2px;
    width: 4px;
    border-radius: 3px;
    transform: rotate(-45deg);
    height: 10px;
    background-color: #c7d0f8; }

.field {
  border: 0;
  width: 100%;
  height: 100%;
  padding: 22px 23px;
  background: white;
  border-radius: 4px;
  transition: all 0.4s ease;
  margin: -2px; }
  .field:focus {
    outline: none; }
    .field:focus + .icons-container .icon-close {
      opacity: 1;
      transform: translateX(0); }
    .field:focus + .icons-container .icon-search {
      opacity: 0;
      transform: translateX(200%); }

.search-bar {
  margin: 0px auto;
  max-width: 281px; }

.search-btn {
  border: none;
  box-shadow: inset 0px 3px 10px -2px rgba(0, 0, 0, 0.2);
  padding: 10px;
  width: 275px;
  background: #fff;
  border-top: 1px #d9eeec dashed; }
  .search-btn:hover {
    cursor: pointer; }

.loading-animation {
  width: 260px;
  margin: 40px auto; }

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

header {
  text-align: center;
  width: 100%;
  height: auto;
  background-size: cover;
  position: relative;
  overflow: hidden; }

header .overlay {
  width: 100%;
  padding: 50px;
  color: #d4d3d3;
  text-shadow: 1px 1px 1px red;
  /* Use "linear-gradient" to add a darken background effect to the image (photographer.jpg). This will make the text easier to read */
  background-image: url("https://i.imgur.com/F1jy0nZ.png");
  /* Set a specific height */
  height: 43vh;
  /* Position and center the image to scale nicely on all screens */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative; }

h1 {
  font-family: "Anton", sans-serif;
  letter-spacing: 3px;
  font-size: 4.5rem;
  margin-bottom: 17px;
  text-transform: uppercase; }

h3 {
  font-family: "Open Sans", sans-serif;
  margin-bottom: 30px; }

.almost-title {
  display: inline-block;
  font-size: 11px;
  transform: translate(36px, -66px) rotate(-14deg);
  text-transform: capitalize; }

.card {
  width: 280px;
  display: block;
  margin: 60px 20px;
  transition: .25s;
  box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.32); }
  .card:hover {
    cursor: pointer; }
    .card:hover .button-primary {
      transform: translate(10px, 0); }

.card-header {
  height: 150px;
  width: 100%;
  padding: 15px;
  width: 100%;
  background-size: cover;
  color: #fff;
  box-sizing: border-box; }

.card-header--title {
  text-transform: uppercase;
  margin: 0; }

.card-body {
  padding: 15px;
  background-color: #fff;
  width: 100%;
  box-sizing: border-box; }

.date {
  font-size: 11px;
  font-weight: 600;
  color: grey;
  margin-bottom: 15px; }

.body-content {
  font-size: 13px;
  line-height: 2; }

.button-primary {
  border: none;
  box-shadow: none;
  font-family: inherit;
  background-color: transparent;
  color: blue;
  font-size: 15px;
  transition: .25s; }
  .button-primary i {
    padding-right: 4px; }

.designer-link {
  color: #fff;
  text-align: center;
  fon-size: 12px;
  text-decoration: none;
  display: block; }

